$(document).ready(function() {

    $(".location__btn").click(function () {
        $(".location").addClass("_all")
        $(".location__nav").fadeOut()
    })



    let points = document.querySelectorAll(".animate-point")

    if(points.length>0){
        points.forEach(triangle => {
            let randomNum = Math.random() * 3;
            triangle.style.animationDelay = `${randomNum}s`
        })
    }

    $(".lang__show").click(function () {
        $(this).parents(".lang").toggleClass("_active")
    })

    $(document).mouseup(function(e) {
        var div = $('.lang');
        if (!div.is(e.target) && div.has(e.target).length === 0) {
            if($(".lang").hasClass("_active")){
                $(".lang").removeClass("_active")
            }
        }
    });

    $(".burger").click(function () {
        $(".header").toggleClass("_open")
        $("body").toggleClass("fixed-body")
    })

    fix100vh()
    $(window).resize(function () {
        fix100vh()
    })
});
function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}
